.ht-hashtag {
  cursor: pointer;
  margin: 0 10px 10px 0;
  background-color: #333;
  color: #fff;
}
.ht-hashtag-container {
  cursor: text;
  position: relative;
  padding: 10px 10px 0;
  border: 1px solid;
  color: #333;
  overflow: auto;
  background: #fff;
  box-sizing: border-box;
}
.ht-hashtag-container--focused {
  border-color: #009998;
}
.ht-hashtag-input {
  border: 0;
  margin: 0 0 10px 0;
  background: none;
  text-align: left;
  outline: none;
}
.ht-hashtag-input .placeholder {
  font-size: 90%;
  letter-spacing: -0.03em;
}
.ht-hashtag-input::-webkit-input-placeholder {
  font-size: 90%;
  letter-spacing: -0.03em;
}
.ht-hashtag-input:-moz-placeholder {
  font-size: 90%;
  letter-spacing: -0.03em;
}
.ht-hashtag-input::-moz-placeholder {
  font-size: 90%;
  letter-spacing: -0.03em;
}
.ht-hashtag-input:-ms-input-placeholder {
  font-size: 90%;
  letter-spacing: -0.03em;
}
.ht-hashtag-input,
.ht-hashtag {
  font-size: 1rem;
  height: 32px;
  line-height: 32px;
  white-space: nowrap;
  float: left;
}
.ht-hashtag:hover {
  background-color: #545454;
}
.ht-hashtag--focused,
.ht-hashtag--focused:hover {
  background-color: #777;
}
.ht-hashtag-label,
.ht-hashtag-remove,
.ht-hashtag-value {
  text-align: center;
  display: inline-block;
  vertical-align: top;
  height: 100%;
}
.ht-hashtag-label,
.ht-hashtag-remove {
  display: inline-block;
}
.ht-hashtag-label {
  padding-left: 8px;
}
.ht-hashtag-value {
  padding: 0 8px;
  max-width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.ht-hashtag-remove {
  padding-right: 8px;
}
